  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Inter", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  color: #e8eef6;
  background: #0b0f15;
}

.stripe {
  padding: 6rem 0;
}
.stripe .container {
  max-width: 1120px;
  width: calc(100% - 2rem);
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
.stripe .container .job {
  text-align: left;
}
.stripe h2 {
  font-size: clamp(1.6rem, 1.2rem + 1.5vw, 2.4rem);
  margin: 0 0 1rem;
}
.stripe p {
  color: #9fb0c7;
}
.stripe li {
  color: #9fb0c7;
}

.site-header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.nav {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 1rem;
  height: 80px;
  padding: 0 1rem;
  background: rgba(12, 16, 24, 0.7);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  transition: height 0.25s ease, padding 0.25s ease, background 0.25s ease;
}
.nav.shrink {
  height: 56px;
  background: rgba(12, 16, 24, 0.9);
}
.nav .brand {
  font-weight: 800;
  font-size: 1.25rem;
  color: #e8eef6;
  text-decoration: none;
}
.nav .nav-toggle {
  display: none;
  font-size: 1.5rem;
  background: transparent;
  color: #e8eef6;
  border: 0;
}
.nav .nav-links {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 0;
  justify-self: center;
}
.nav .nav-links a {
  color: #9fb0c7;
  text-decoration: none;
  padding: 0.5rem 0.75rem;
  border-radius: 0.75rem;
  transition: background 0.2s, color 0.2s;
}
.nav .nav-links a.active, .nav .nav-links a:hover {
  color: #e8eef6;
  background: rgba(255, 255, 255, 0.06);
}
.nav .progress {
  position: absolute;
  inset-inline: 0;
  bottom: 0;
  height: 3px;
  background: rgba(255, 255, 255, 0.06);
}
.nav .progress .bar {
  display: block;
  height: 100%;
  width: 0%;
  background: linear-gradient(90deg, #6ea8fe, #7ce3b1);
  transition: width 0.1s linear;
}

.hero {
  min-height: calc(100vh - 80px);
  display: grid;
  place-items: center;
}

.center-vert {
  display: grid;
}

.center-vert-child {
  display: grid;
  place-items: center;
  min-height: 100%;
}

.hero-content {
  text-align: center;
}

.title {
  font-size: clamp(2rem, 2rem + 3vw, 4rem);
  margin: 0 0 0.5rem;
}

.subtitle {
  color: #9fb0c7;
  margin: 0 0 1.5rem;
}

.cta-row {
  display: inline-flex;
  gap: 0.75rem;
}

.btn {
  display: inline-block;
  padding: 0.8rem 1.1rem;
  border-radius: 1rem;
  text-decoration: none;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(255, 255, 255, 0.04);
  color: #e8eef6;
  transition: transform 0.15s ease, background 0.2s ease, color 0.2s ease;
}
.btn:hover {
  transform: translateY(-2px);
}
.btn.primary {
  display: inline-block;
  padding: 0.8rem 1.1rem;
  border-radius: 1rem;
  text-decoration: none;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: #6ea8fe;
  color: #0b1220;
  transition: transform 0.15s ease, background 0.2s ease, color 0.2s ease;
}
.btn.primary:hover {
  transform: translateY(-2px);
}
.btn.ghost {
  display: inline-block;
  padding: 0.8rem 1.1rem;
  border-radius: 1rem;
  text-decoration: none;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: transparent;
  color: #e8eef6;
  transition: transform 0.15s ease, background 0.2s ease, color 0.2s ease;
}
.btn.ghost:hover {
  transform: translateY(-2px);
}
.btn.small {
  padding: 0.6rem 0.9rem;
  font-size: 0.9rem;
}

.socials {
  margin-top: 1rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-direction: row;
  width: -moz-max-content;
  width: max-content;
  margin-left: auto;
  margin-right: auto;
}
.socials .icon {
  width: 38px;
  height: 38px;
  display: grid;
  place-items: center;
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 0.8rem;
  color: #e8eef6;
  transition: transform 0.15s;
}
.socials .icon svg {
  width: 20px;
  height: 20px;
  fill: #e8eef6;
}
.socials .icon:hover {
  transform: translateY(-2px);
}

.experience .job {
  text-align: left;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 1rem;
  padding: 1rem 1.25rem;
  margin-bottom: 1rem;
  transition: transform 0.15s ease, box-shadow 0.2s ease, border-color 0.2s ease;
}

.experience .job:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.25);
}

.parallax {
  position: relative;
  padding: 0;
  height: 48vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}
.parallax .parallax-overlay {
  position: absolute;
  inset: 0;
  display: grid;
  place-items: center;
  background: linear-gradient(180deg, rgba(11, 15, 21, 0.5), rgba(11, 15, 21, 0.8));
}
.parallax .parallax-overlay .overlay-title {
  font-size: clamp(1.4rem, 1.2rem + 2vw, 2.6rem);
}

.columns {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  text-align: left;
}

.col .card {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 1rem;
  padding: 1rem;
  min-height: 100%;
}

.badges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  padding: 0;
  margin: 0.75rem 0 0;
  list-style: none;
}
.badges li {
  padding: 0.35rem 0.6rem;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.12);
  font-size: 0.9rem;
  color: #e8eef6;
}

.carousel {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 0.5rem;
}

.carousel .viewport {
  overflow: hidden;
}

.carousel .track {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 100%;
  transition: transform 0.4s ease;
}

.carousel .slide {
  padding: 1.25rem;
  margin: 0.25rem;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 1rem;
  text-align: left;
}

.carousel .arrow {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.06);
  color: #e8eef6;
  cursor: pointer;
  transition: transform 0.15s;
}

.carousel .arrow:hover {
  transform: translateY(-2px);
}

.carousel .dots {
  display: flex;
  gap: 0.4rem;
  justify-content: center;
  margin-top: 0.75rem;
  grid-column: 1/-1;
}

.carousel .dots button {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  border: 0;
  background: rgba(255, 255, 255, 0.25);
}

.carousel .dots button[aria-selected=true] {
  background: #6ea8fe;
}

.grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0.75rem;
}

.tile {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 1rem;
  padding: 1.25rem;
  display: grid;
  place-items: center;
  gap: 0.5rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.tile:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
}

.tile img {
  -o-object-fit: contain;
     object-fit: contain;
  width: 100px;
}

.icon-xl {
  font-size: 2rem;
}

.video-wrap video {
  width: 100%;
  max-width: 960px;
  border-radius: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.12);
}

.footer {
  padding: 2rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}
.footer .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer .footer-icons .icon {
  width: 38px;
  color: white;
  height: 38px;
  display: grid;
  place-items: center;
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 0.8rem;
}

.modal {
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  background: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}

.modal[hidden] {
  display: none;
}

.modal .modal-content {
  width: min(92vw, 640px);
  background: #111623;
  color: #e8eef6;
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 1rem;
  padding: 1rem 1.2rem;
}

.modal .close {
  position: absolute;
  right: 1.2rem;
  top: 1.2rem;
  background: transparent;
  color: #e8eef6;
  border: 0;
  font-size: 1.5rem;
  cursor: pointer;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.hero .title, .hero .subtitle, .cta-row {
  animation: fadeInUp 0.6s ease both;
}

.hero .subtitle {
  animation-delay: 0.08s;
}

.hero .cta-row {
  animation-delay: 0.16s;
}

@media (max-width: 1024px) {
  .columns {
    grid-template-columns: 1fr 1fr;
  }

  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 768px) {
  .nav {
    grid-template-columns: auto auto;
  }
  .nav .nav-toggle {
    display: inline-block;
  }
  .nav .nav-links {
    display: none;
  }
  .nav .open .nav-links {
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: rgba(12, 16, 24, 0.98);
    padding: 0.75rem;
    justify-content: center;
  }

  .columns {
    grid-template-columns: 1fr;
  }

  .contact-form .row {
    grid-template-columns: 1fr;
  }

  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.nav {
  font-size: 1rem;
}

.nav.shrink {
  font-size: 0.95rem;
}

:focus-visible {
  outline: 2px solid #6ea8fe;
  outline-offset: 2px;
