n    background: rgba(12, 16, 24, 0.9); }\n  .brand { \n    font-weight: 800; \n    font-size: 1.25rem; \n    color: $fg; \n    text-decoration: none; }\n  .nav-toggle { \n    display: none; \n    font-size: 1.5rem; \n    background: transparent; \n    color: $fg; \n    border: 0; }\n  .nav-links {\n    list-style: none; \n    display: flex; \n    gap: 1rem; \n    margin: 0; \n    padding: 0; \n    justify-self: center;\n    a {\n       color: #9fb0c7; \n       text-decoration: none; \n       padding: .5rem .75rem; \n       border-radius: .75rem; \n       transition: background .2s, color .2s; }\n    a.active, a:hover { \n      color: $fg; \n      background: rgba(255,255,255,.06); }\n  }\n  .progress { \n    position: absolute; \n    inset-inline: 0; \n    bottom: 0; \n    height: 3px; \n    background: rgba(255,255,255,.06);\n    .bar { \n      display: block; \n      height: 100%; \n      width: 0%; \n      background: linear-gradient(90deg, $accent, $accent2); \n      transition: width .1s linear; }\n  }\n}\n\n.hero { \n  min-height: calc(100vh - #{$nav-large}); \n  display: grid; \n  place-items: center; }\n\n.center-vert { display: grid; }\n\n.center-vert-child { display: grid; \n  place-items: center; \n  min-height: 100%; }\n\n.hero-content { text-align: center; }\n\n.title { \n  font-size: clamp(2rem, 2rem + 3vw, 4rem); \n  margin: 0 0 .5rem; }\n\n.subtitle { \n  color: #9fb0c7; \n  margin: 0 0 1.5rem; }\n\n.cta-row { \n  display: inline-flex; \n  gap: .75rem; }\n\n.btn { @include button(rgba(255,255,255,.04), $fg);\n  &.primary { @include button($accent, #0b1220); }\n  &.ghost { @include button(transparent, $fg); }\n  &.small { padding: .6rem .9rem; font-size: .9rem; }\n}\n\n.socials { \n  margin-top: 1rem; \n  display: flex; \n  gap: .5rem; \n  align-items: center; \n  flex-direction: row; \n  width: max-content; \n  margin-left: auto; \n  margin-right: auto;\n  .icon { \n    width: 38px; \n    height: 38px; \n    display: grid; \n    place-items: center; \n    border: 1px solid rgba(255,255,255,.12); \n    border-radius: .8rem; color: $fg; \n    transition: transform .15s;\n    svg { width: 20px; height: 20px; fill: $fg; }\n    &:hover { transform: translateY(-2px); }\n  }\n}\n.experience .job {\n  text-align: left;\n  background: rgba(255,255,255,.04);\n  border: 1px solid rgba(255,255,255,.12);\n  border-radius: 1rem;\n  padding: 1rem 1.25rem;\n  margin-bottom: 1rem;\n  transition: transform .15s ease, box-shadow .2s ease, border-color .2s ease;\n}\n\n.experience .job:hover {\n  transform: translateY(-2px);\n  box-shadow: 0 8px 24px rgba(0,0,0,.25);\n}\n\n.parallax {\n  position: relative;\n  padding: 0;\n  height: 48vh;\n  background-image: url('../assets/image.png');\n  background-attachment: fixed;\n  background-size: cover;\n  background-position: center;\n  .parallax-overlay {\n    position: absolute; inset: 0; display: grid; place-items: center;\n    background: linear-gradient(180deg, rgba(11,15,21,.5), rgba(11,15,21,.8));\n    .overlay-title { font-size: clamp(1.4rem, 1.2rem + 2vw, 2.6rem); }\n  }\n}\n\n.columns { \n  display: grid; \n  grid-template-columns: repeat(3, 1fr); \n  gap: 1rem; \n  text-align: left; }\n\n.col .card { \n  background: rgba(255,255,255,.04); \n  border: 1px solid rgba(255,255,255,.08); \n  border-radius: 1rem; \n  padding: 1rem; \n  min-height: 100%; }\n\n.badges { \n  display: flex; \n  flex-wrap: wrap; \n  gap: .5rem; \n  padding: 0; \n  margin: .75rem 0 0; \n  list-style: none;\n  li { \n    padding: .35rem .6rem; \n    border-radius: 999px; \n    border: 1px solid rgba(255,255,255,.12); \n    font-size: .9rem; \n    color: $fg; }\n}\n\n.carousel { \n  position: relative; \n  display: grid; \n  grid-template-columns: auto 1fr auto; \n  align-items: center; gap: .5rem; }\n\n.carousel .viewport { overflow: hidden; }\n\n.carousel .track { \n  display: grid; \n  grid-auto-flow: column; \n  grid-auto-columns: 100%; \n  transition: transform .4s ease; }\n\n.carousel .slide { \n  padding: 1.25rem; \n  margin: .25rem; \n  background: rgba(255,255,255,.04); \n  border: 1px solid rgba(255,255,255,.08); \n  border-radius: 1rem; text-align: left; }\n\n.carousel .arrow { \n  width: 44px; \n  height: 44px; \n  border-radius: 50%; \n  border: 1px solid rgba(255,255,255,.2); \n  background: rgba(255,255,255,.06); \n  color: $fg; cursor: pointer; \n  transition: transform .15s; }\n\n.carousel .arrow:hover { transform: translateY(-2px); }\n\n.carousel .dots { \n  display: flex; \n  gap: .4rem; justify-content: center; \n  margin-top: .75rem; \n  grid-column: 1 / -1; }\n\n.carousel .dots button { \n  width: 10px; \n  height: 10px; \n  border-radius: 999px; \n  border: 0; \n  background: rgba(255,255,255,.25); }\n\n.carousel .dots button[aria-selected=\"true\"] { background: $accent; }\n\n.grid { \n  display: grid; \n  grid-template-columns: repeat(6,1fr); gap: .75rem; }\n\n.tile { \n  background: rgba(255,255,255,.04); \n  border: 1px solid rgba(255,255,255,.08); \n  border-radius: 1rem; padding: 1.25rem; \n  display: grid; \n  place-items: center; \n  gap: .5rem; \n  transition: transform .2s ease, box-shadow .2s ease ;\n  &:hover { transform: translateY(-4px); box-shadow: 0 10px 30px rgba(0,0,0,.25); }\n}\n\n.tile img {object-fit: contain; width: 100px;}\n.icon-xl { font-size: 2rem; }\n\n.video-wrap video { \n  width: 100%; \n  max-width: 960px; \n  border-radius: .75rem; \n  border: 1px solid rgba(255,255,255,.12); }\n\n.footer { \n  padding: 2rem 0; \n  border-top: 1px solid rgba(255,255,255,.08);\n  .container { \n    display: flex; \n    align-items: center; \n    justify-content: space-between; }\n  .footer-icons .icon { \n    width: 38px; \n    color: white;\n    height: 38px; \n    display: grid; \n    place-items: center;\n    border: 1px solid rgba(255,255,255,.12); \n    border-radius: .8rem; }\n}\n\n.modal { \n  position: fixed; \n  inset: 0; \n  display: grid; \n  place-items: center; \n  background: rgba(0,0,0,.5); \n  padding: 1rem; }\n\n.modal[hidden] { display: none; }\n\n.modal .modal-content { \n  width: min(92vw, 640px); \n  background: #111623; \n  color: $fg; \n  border: 1px solid rgba(255,255,255,.12); \n  border-radius: 1rem; padding: 1rem 1.2rem; }\n\n.modal .close { \n  position: absolute; \n  right: 1.2rem; \n  top: 1.2rem; \n  background: transparent; \n  color: $fg; \n  border: 0; \n  font-size: 1.5rem; \n  cursor: pointer; }\n\n@keyframes fadeInUp {\n  from { opacity: 0; transform: translateY(10px); }\n  to { opacity: 1; transform: translateY(0); }\n}\n.hero .title, .hero .subtitle, .cta-row { animation: fadeInUp .6s ease both; }\n.hero .subtitle { animation-delay: .08s; }\n.hero .cta-row { animation-delay: .16s; }\n\n@media (max-width: 1024px) {\n  .columns { grid-template-columns: 1fr 1fr; }\n  .grid { grid-template-columns: repeat(3,1fr); }\n}\n\n@media (max-width: 768px) {\n  .nav { \n    grid-template-columns: auto auto; \n    .nav-toggle { display: inline-block; } \n    .nav-links { display: none; } &\n    .open .nav-links { \n      display: flex; \n      position: absolute; \n      top: 100%; \n      left: 0; \n      right: 0; \n      background: rgba(12,16,24,.98); \n      padding: .75rem; \n      justify-content: center; } }\n  .columns { grid-template-columns: 1fr; }\n  .contact-form .row { grid-template-columns: 1fr; }\n  .grid { grid-template-columns: repeat(2,1fr); }\n}\n\n.nav { font-size: 1rem; }\n.nav.shrink { font-size: .95rem; }\n\n:focus-visible { outline: 2px solid $accent; outline-offset: 2px; }\n"],"sourceRoot":""}]);
